
.content {
    box-sizing: border-box;
    width: 100%;
    padding: 10px;
    display: flex;
    flex-wrap: wrap;
}

.card {
    width: 250px;
    height: 300px;
    border-radius: 20px;
    box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
    overflow: hidden;
    float: left;
}

.card {
    margin-right: 15px;
    margin-bottom: 15px;
}

.card > img {
    width: 100%;
    height: 60%;
    object-fit: cover;
}

.card > p {
    margin: 0;
    padding: 5px 0;
    padding-left: 5px;
    text-align: center;
}

.card > button {
    position: relative;
    translate: 90%;
    padding: 8px;
    background-color: yellow;
    border-radius: 20px;
    border: 2px solid grey;
    font-weight: bold;
}

.card > button:hover {
    background-color: crimson;
    color: #fff;
} 